import { useState } from "react";
import { FaceDetector } from "./components/FaceDetector";
import { ReferenceCreator } from "./components/ReferenceCreator";
import styles from "./styles/App.module.css";

function App() {
  const [descriptors, setDescriptors] = useState<any>(null);
  const [view, setView] = useState("reference");

  return (
    <div className={styles.app}>
      <div className={styles.container}>
        {view === "reference" && (
          <ReferenceCreator
            onFinish={(des: any) => {
              setView("detect");
              setDescriptors(des);
            }}
          />
        )}
        {view === "detect" && <FaceDetector descriptors={descriptors} />}
      </div>
    </div>
  );
}

export default App;
