import React, { useEffect, useRef, useState } from "react";
import * as faceapi from "face-api.js";
import loading from "../images/loading.gif";
import styles from "../styles/FaceDetector.module.css";

interface Props {
  descriptors: any;
}

enum MODEL_STATE {
  NULL,
  LOADING,
  READY,
}

export const FaceDetector: React.FC<Props> = (props: Props) => {
  const inputRef = useRef<HTMLVideoElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);
  const [loadState, setLoadState] = useState<MODEL_STATE>(MODEL_STATE.NULL);
  const [isPlaying, setPlaying] = useState(false);
  const [detectionResult, setDetectionResult] = useState("");

  const { descriptors } = props;

  /**
   * Start webcam video
   */
   const startVideo = () => {
    setLoadState(MODEL_STATE.READY);

    navigator.mediaDevices
      .getUserMedia({ video: true })
      .then((stream) => {
        if (inputRef.current) inputRef.current.srcObject = stream;
      })
      .catch((err) => console.error(err));
  };

  //Run the detection
  useEffect(() => {

    let interval: any;

    //Create canvas
    if(isPlaying && inputRef.current){
      const video = inputRef.current;
      const canvas = faceapi.createCanvasFromMedia(video);
      if(containerRef.current) containerRef.current.appendChild(canvas);
      const displaySize = { width: video.width, height: video.height };
      faceapi.matchDimensions(canvas, displaySize);
    

    interval = setInterval(async () => {
      if (isPlaying && inputRef.current) {
        //Detect
        const detections = await faceapi
          .detectAllFaces(
            inputRef.current,
            new faceapi.TinyFaceDetectorOptions()
          )
          .withFaceLandmarks()
          .withFaceDescriptors();

          if(descriptors){
            const faceMatcher = new faceapi.FaceMatcher(descriptors);
  
            detections.forEach(fd => {
              const bestMatch = faceMatcher.findBestMatch(fd.descriptor)
              console.log(bestMatch.toString())
              setDetectionResult(bestMatch.toString());
            });
          }
  
          const resizedDetections = faceapi.resizeResults(
            detections,
            displaySize
          );
          (canvas.getContext("2d") as any).clearRect(0, 0, canvas.width, canvas.height);
          faceapi.draw.drawDetections(canvas, resizedDetections);
          faceapi.draw.drawFaceLandmarks(canvas, resizedDetections);
      }
    }, 100);
  }

    return () => clearTimeout(interval);
  }, [isPlaying, descriptors]);

  const onPlay = () => {
    setPlaying(true);
  };

  //Load the models
  useEffect(() => {
    setLoadState(MODEL_STATE.LOADING);
    Promise.all([
      faceapi.nets.tinyFaceDetector.loadFromUri(process.env.PUBLIC_URL + "/models"),
      faceapi.nets.faceLandmark68Net.loadFromUri(process.env.PUBLIC_URL + "/models"),
      faceapi.nets.faceRecognitionNet.loadFromUri(process.env.PUBLIC_URL + "/models"),
      faceapi.nets.faceExpressionNet.loadFromUri(process.env.PUBLIC_URL + "/models"),
    ]).then(startVideo);
  }, []);

  return (
    <div className={styles.container}>
      <h2 style={{margin: 0, marginBottom: "5px"}}>Gesichtserkennung</h2>
      <p style={{fontSize: "13px", marginBlock: "5px"}}>
        Ihr Gesicht sollte nun erkannt werden. 
        In der oberen linken Ecke sollte Sie nun Ihren Namen sehen (oder User).
        Für nicht erkannte Personen wird "unknown" angezeigt.
      </p>
      <div className={styles.videoContainer} ref={containerRef}>
        <video
          ref={inputRef}
          autoPlay
          muted
          onPlay={onPlay}
          width="720"
          height="560"
        ></video>
        {isPlaying && <div className={styles.detectionResult}>Erkannt: {detectionResult}</div>}
        {!isPlaying && (
          <div className={styles.videoContainerHover}>
            {loadState !== MODEL_STATE.READY && (
              <div className={styles.loadingContainer}>
                <img src={loading} alt="loading" className={styles.loading} />
                <p className={styles.loadingText}>Laden...</p>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};
