import React, { useEffect, useRef, useState } from "react";
import * as faceapi from "face-api.js";
import styles from "../styles/FaceDetector.module.css";
import loading from "../images/loading.gif";
import { ProgressIndicator } from "./ProgressIndicator";

interface Props {
  onFinish: (labels: any) => void;
}

enum MODEL_STATE {
  NULL,
  LOADING,
  READY,
}

export const ReferenceCreator: React.FC<Props> = (props: Props) => {
  const inputRef = useRef<HTMLVideoElement>(null);
  const [loadState, setLoadState] = useState<MODEL_STATE>(MODEL_STATE.NULL);
  const [isPlaying, setPlaying] = useState(false);
  const [isRunning, setRunning] = useState(false);
  const [descriptors, setDescriptors] = useState<any>([]);
  const [name, setName] = useState("User");

  const { onFinish } = props;

  /**
   * Start webcam video
   */
  const startVideo = () => {
    setLoadState(MODEL_STATE.READY);

    navigator.mediaDevices
      .getUserMedia({ video: true })
      .then((stream) => {
        if (inputRef.current) inputRef.current.srcObject = stream;
      })
      .catch((err) => console.error(err));
  };

  //Run the detection
  useEffect(() => {
    const interval = setInterval(async () => {
      if (isPlaying && isRunning && inputRef.current) {
        //Detect
        const detections = await faceapi
          .detectAllFaces(
            inputRef.current,
            new faceapi.TinyFaceDetectorOptions()
          )
          .withFaceLandmarks()
          .withFaceDescriptors();

        //Check if there are more than one person
        if (detections.length === 1) {
          setDescriptors((old: any) => [...old, detections[0].descriptor]);
        } else {
          console.log("Bitte alleine im Bild sein");
        }

        //Continue after ten images
        if (descriptors.length > 10) {
          onFinish([new faceapi.LabeledFaceDescriptors(name, descriptors)]);
        }
      }
    }, 1000);

    return () => clearTimeout(interval);
  }, [isPlaying, isRunning, descriptors, name, onFinish]);

  const onPlay = () => {
    setPlaying(true);
  };

  //Load the models
  useEffect(() => {
    setLoadState(MODEL_STATE.LOADING);
    Promise.all([
      faceapi.nets.tinyFaceDetector.loadFromUri(process.env.PUBLIC_URL + "/models"),
      faceapi.nets.faceLandmark68Net.loadFromUri(process.env.PUBLIC_URL + "/models"),
      faceapi.nets.faceRecognitionNet.loadFromUri(process.env.PUBLIC_URL + "/models"),
      faceapi.nets.faceExpressionNet.loadFromUri(process.env.PUBLIC_URL + "/models"),
    ]).then(startVideo);
  }, []);

  return (
    <div className={styles.container}>
      <h2 style={{margin: 0, marginBottom: "5px"}}>Referenzbilder aufnehmen</h2>
      <p style={{fontSize: "13px", marginBlock: "5px"}}>
        Bitte bewegen Sie Ihren Kopf leicht umher, nachdem Sie unten auf "Start"
        gedrückt haben. Optional können Sie Ihren Namen angeben, dieser wird im
        zweiten Schritt angezeigt, wenn Ihr Gesicht erkannt wird. Wenn Sie
        keinen Namen angeben werden Sie als "User" erkannt. Warten Sie, bis sicher der Balken oben gefüllt hat.
      </p>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          marginBottom: "10px",
        }}
      >
        <label
          htmlFor="nameInput"
          style={{ fontWeight: "bold", fontSize: "13px" }}
        >
          Name (optional)
        </label>
        <input
          type="text"
          name="nameInput"
          value={name}
          onChange={(e: any) => setName(e.target.value)}
          className={styles.nameInput}
        />
      </div>

      <div className={styles.videoContainer}>
        <video
          ref={inputRef}
          autoPlay
          muted
          onPlay={onPlay}
          width="720"
          height="560"
        ></video>

        {isRunning && <div className={styles.progressContainer}><ProgressIndicator value={(descriptors.length/10)*100}/></div>}
        {!isRunning && (
          <div className={styles.videoContainerHover}>
            {loadState !== MODEL_STATE.READY && (
              <div className={styles.loadingContainer}>
                <img src={loading} alt="loading" className={styles.loading} />
                <p className={styles.loadingText}>Laden...</p>
              </div>
            )}
            {loadState === MODEL_STATE.READY && <button className={styles.startButton} onClick={() => setRunning(true)}>Start</button>}
          </div>
        )}
      </div>
    </div>
  );
};
